}
:root {
    --melon: #FFA69E; 
    --eggshell: #FAF3DD;
    --teal: #B8F2E6;
    --lightblue: #C3F0FB;
    --blue: #c3f0fb;
    --darkblue: #7cb7d7;
    --blackcoral: #5E6472;
}

.button:hover{
  background-color: #c3f0fb !important;
  background-color: var(--blue) !important;
  border-color: white !important;
  color: #5E6472 !important;
  color: var(--blackcoral) !important;
  outline: 0 !important;
}
.button{
  color: #7cb7d7;
  color: var(--darkblue);
  background-color: white;
  border-color: #7cb7d7;
  border-color: var(--darkblue);
  width: -webkit-fit-content;
  width: fit-content;
  justify-content: center;
  outline: 0 !important;
  outline-style: none;
  box-shadow: none !important;
  margin: 5px;
}

:-moz-any-link:focus {
  outline: none;
}

.button.launch{
  border: none;
}

.button.focus, .button:focus {
  background-color: #c3f0fb !important;
  background-color: var(--blue) !important;
  /* border-color: white !important; */
  color: #5E6472 !important;
  color: var(--blackcoral) !important;
}

.button:active {
  background-color: #c3f0fb !important;
  background-color: var(--blue) !important;
  /* border-color: white !important; */
  color: #5E6472 !important;
  color: var(--blackcoral) !important;
}

*:focus {
  outline: 0 !important;
  border: none !important;
  -webkit-box-shadow: none !important;
}

body{
  font-family: 'Comfortaa', cursive !important;
}

header{
  text-align: center;
  color: white;
  font-weight: 600;
}

h1{
  text-align: center;
  color: #7cb7d7;
  color: var(--darkblue);
}

h2{
  text-align: center;
  color: white;
}

h3{
  text-align: center;
  color: white;
}

h5{
  color: #7cb7d7;
  color: var(--darkblue);
}

h6{
  color: #5E6472;
  color: var(--blackcoral);
  text-align: center;
  font-weight: lighter;
}
.p{
  color: #5E6472;
  color: var(--blackcoral);
  line-height: 4;
  margin-bottom: 3px;
}

.paragraph{
  color: #5E6472;
  color: var(--blackcoral);
  font-style: italic;
  font-weight: lighter;
  vertical-align: middle;
}

